import { animated, useSpring } from '@react-spring/web';
import dropDownArrow from 'options/images/drop-down-arrow.svg';
import React, { useLayoutEffect, useRef, useState } from 'react';
import reactStringReplace from 'react-string-replace';
import { L10nKey } from 'types';
import { t } from 'utils';
import * as S from './styles';

type Props = {
  faqId: number;
  contentCount: number;
};

export default function DropDown({ faqId, contentCount }: Props) {
  const [toggle, setToggle] = useState(false);
  const contentsRef = useRef<null | HTMLDivElement>(null);
  const contentHeightRef = useRef<number>(0);
  const spring = useSpring({
    height: toggle ? contentHeightRef.current : 0,
  });
  useLayoutEffect(() => {
    contentHeightRef.current = contentsRef.current?.scrollHeight || 0;
  }, []);

  const resolveContentLinks = (faqId: number, contentId: number) => {
    if (faqId === 5 && contentId === 1) {
      // Windows __LINK_1__
      // Mac __LINK_2__
      return reactStringReplace(
        t(`faq_headline_${faqId}_content_${contentId}` as L10nKey),
        /(__.+__)/g,
        (match, index) => (
          <a
            key={index}
            href={(() => {
              switch (match) {
                case '__LINK_1__':
                  return 'https://support.microsoft.com/windows/%E8%AE%8A%E6%9B%B4%E9%80%9A%E7%9F%A5%E5%92%8C%E5%BF%AB%E9%80%9F%E8%A8%AD%E5%AE%9Awindows-11-ddcbbcd4-0a02-f6e4-fe14-6766d850f294';
                case '__LINK_2__':
                  return 'https://support.apple.com/HT204079';
              }
            })()}
            target="_blank"
            rel="noreferrer noopener"
          >
            {t('faq_link_here')}
          </a>
        )
      );
    }

    return t(`faq_headline_${faqId}_content_${contentId}` as L10nKey);
  };

  return (
    <S.DropDown>
      <S.Title onClick={() => setToggle((state) => !state)}>
        {t(`faq_headline_${faqId}` as L10nKey)}
        <S.ArrowImage src={dropDownArrow} toggle={toggle} />
      </S.Title>
      <S.Contents as={animated.div} ref={contentsRef} style={spring}>
        {[...Array(contentCount)].map((_, index) => (
          <S.ContentGroup key={index}>
            <S.SubTitle>
              {t(`faq_headline_${faqId}_title_${index + 1}` as L10nKey)}
            </S.SubTitle>
            <S.Divider />
            <S.Content>{resolveContentLinks(faqId, index + 1)}</S.Content>
          </S.ContentGroup>
        ))}
      </S.Contents>
    </S.DropDown>
  );
}
