import { Route } from 'options/routes';
import React from 'react';
import { t } from 'utils';
import * as S from './styles';

type Props = {
  currentRoute: Route;
  setCurrentRoute: React.Dispatch<React.SetStateAction<Route>>;
};

export default function NavBar({ currentRoute, setCurrentRoute }: Props) {
  return (
    <S.NavBar>
      <S.NavLinks>
        <S.NavLink
          isActive={currentRoute === 'History'}
          onClick={() => setCurrentRoute('History')}
        >
          {t('settings_sidebar_history')}
        </S.NavLink>
        <S.NavLink
          isActive={currentRoute === 'Blocklist'}
          onClick={() => setCurrentRoute('Blocklist')}
        >
          {t('settings_sidebar_blocklist')}
        </S.NavLink>
        <S.NavLink
          isActive={currentRoute === 'Allowlist'}
          onClick={() => setCurrentRoute('Allowlist')}
        >
          {t('settings_sidebar_allowlist')}
        </S.NavLink>
        <S.NavLink
          isActive={currentRoute === 'About'}
          onClick={() => setCurrentRoute('About')}
        >
          {t('settings_sidebar_aboutforest')}
        </S.NavLink>
        <S.NavLink
          isActive={currentRoute === 'FAQ'}
          onClick={() => setCurrentRoute('FAQ')}
        >
          {t('settings_sidebar_faq')}
        </S.NavLink>
      </S.NavLinks>
    </S.NavBar>
  );
}
