import * as API from 'api';
import useDialogTrans from 'hooks/useDialogTrans';
import useFadeInOut from 'hooks/useFadeInOut';
import logo from 'images/forest.svg';
import { useAtom } from 'jotai';
import { useUpdateAtom } from 'jotai/utils';
import type { Message } from 'messages';
import React, { Suspense, useEffect, useState } from 'react';
import { createRoot } from 'react-dom/client';
import * as storage from 'storage';
import { t } from 'utils';
import browser from 'webextension-polyfill';
import {
  isInChinaMigrationAtom,
  plantingStatusAtom,
  plantRecordsAtom,
  showToastAtom,
  siteListUpdateAtom,
  tagsAtom,
  userAtom,
} from './atom';
import BlockLoginDialog from './components/BlockLoginDialog';
import LoginDialog from './components/LoginDialog';
import NavBar from './components/NavBar';
import Toast from './components/Toast';
import UserInfo from './components/UserInfo';
import About from './pages/About';
import Faq from './pages/Faq';
import History from './pages/History';
import SiteList from './pages/SiteList';
import routes, { Route } from './routes';
import * as S from './styles';

const Options = () => {
  const loader = useFadeInOut();
  const blockLoginDialog = useDialogTrans();
  const showToast = useUpdateAtom(showToastAtom);
  const [currentRoute, setCurrentRoute] = useState<Route>(routes[0]);
  const setSiteList = useUpdateAtom(siteListUpdateAtom);
  const setPlantingStatus = useUpdateAtom(plantingStatusAtom);
  const [isInChinaMigration, setIsInChinaMigration] = useAtom(
    isInChinaMigrationAtom
  );
  const setPlantRecords = useUpdateAtom(plantRecordsAtom);
  const setUser = useUpdateAtom(userAtom);
  const setTags = useUpdateAtom(tagsAtom);

  useEffect(() => {
    isInChinaMigration && showToast(t('settings_toast_migrtion'));
  }, [isInChinaMigration, showToast]);

  useEffect(() => {
    browser.runtime.onMessage.addListener((message: Message) => {
      switch (message.type) {
        case 'OPTIONS_PAGE_REVALIDATE_SITE_LIST':
          return setSiteList([
            message.payload.siteBlockingMode,
            message.payload.siteList,
          ]);
        case 'UPDATE_PLANTING_STATUS':
          return setPlantingStatus(message.payload);
        case 'RELOAD_OPTIONS_PAGE':
          return location.reload();
      }
    });
    browser.storage.onChanged.addListener((changes) => {
      const { plantRecords, isInChinaMigration, user, tags } = changes;
      plantRecords && setPlantRecords(plantRecords.newValue);
      isInChinaMigration && setIsInChinaMigration(isInChinaMigration.newValue);
      user && setUser(user.newValue);
      tags && setTags(tags.newValue);
    });
  }, []);

  return (
    <>
      <S.GlobalStyle />
      <S.Wrapper>
        <S.TitleBar>
          <S.Logo alt="Logo" src={logo} />
          <S.Title>{t('settings_title')}</S.Title>
        </S.TitleBar>
        <S.Options>
          <UserInfo globalLoader={loader} />
          <S.Body>
            <NavBar
              currentRoute={currentRoute}
              setCurrentRoute={setCurrentRoute}
            />
            <S.RouteContent>
              {currentRoute === 'History' && <History />}
              {currentRoute === 'About' && <About />}
              {currentRoute === 'FAQ' && <Faq />}
              {currentRoute === 'Allowlist' && (
                <SiteList siteBlockingMode="Allow" />
              )}
              {currentRoute === 'Blocklist' && (
                <SiteList siteBlockingMode="Block" />
              )}
            </S.RouteContent>
          </S.Body>
          <LoginDialog showBlockLoginDialog={blockLoginDialog.on} />
          {blockLoginDialog.trans(
            (style, canShow) =>
              canShow && (
                <BlockLoginDialog style={style} close={blockLoginDialog.off} />
              )
          )}
          {loader.trans(
            (style, canShow) => canShow && <S.Loader style={style} />
          )}
          <Toast />
        </S.Options>
      </S.Wrapper>
    </>
  );
};

(async function start() {
  if (process.env['NODE_ENV'] === 'development') {
    browser.storage.local.get().then((d) => console.log(d));
  }
  const serverRegion = await storage.getData('serverRegion');
  API.setAPIBaseURLByServerRegion(serverRegion);
  const rootDiv = document.getElementById('root');
  if (rootDiv === null) return;
  createRoot(rootDiv).render(
    <Suspense fallback={<></>}>
      <Options />
    </Suspense>
  );
})();
