import { SpringValue } from '@react-spring/web';
import React from 'react';
import { t } from 'utils';
import * as S from './styles';

type Props = {
  close: () => void;
  style: { opacity: SpringValue<number>; transform: SpringValue<string> };
};

export default function BlockLoginDialog({ close, style }: Props) {
  return (
    <S.DimView
      style={{ opacity: style.opacity }}
      onClick={({ target, currentTarget }) =>
        target === currentTarget && close()
      }
    >
      <S.Dialog style={{ transform: style.transform }}>
        <S.Title>{t('dialog_login_migration_transfer_title')}</S.Title>
        <S.Content>{t('dialog_login_migration_transfer_content')}</S.Content>
        <S.BtnOK onClick={close}>{t('ok_btn')}</S.BtnOK>
      </S.Dialog>
    </S.DimView>
  );
}
