import { useSpring, useTransition } from '@react-spring/web';
import * as API from 'api';
import type { AxiosError } from 'axios';
import useAsyncInitState from 'hooks/useAsyncInitState';
import useDialogTrans from 'hooks/useDialogTrans';
import useFadeInOut from 'hooks/useFadeInOut';
import { useAtom } from 'jotai';
import { useUpdateAtom } from 'jotai/utils';
import { canShowLoginDialogAtom, showToastAtom } from 'options/atom';
import syncPlants from 'options/syncPlants';
import React, { useRef, useState } from 'react';
import * as storage from 'storage';
import type { ServerRegion, UserLoginData } from 'types';
import { t } from 'utils';
import * as S from './styles';

type Props = {
  showBlockLoginDialog: () => void;
};

export default function LoginDialog({ showBlockLoginDialog }: Props) {
  const isLoadingRef = useRef(false);
  const showToast = useUpdateAtom(showToastAtom);
  const [canShow, setCanShow] = useAtom(canShowLoginDialogAtom);
  const close = () => setCanShow(false);
  const trans = useTransition(canShow, {
    from: { opacity: 0, transform: 'scale(0.7)' },
    enter: { opacity: 1, transform: 'scale(1)' },
    leave: { opacity: 0, transform: 'scale(0.7)' },
  });
  const loader = useFadeInOut();
  const [account, setAccount] = useState<UserLoginData>({
    email: '',
    password: '',
  });
  const [errorMessage, setErrorMessage] = useState<string | null>(null);
  const [formAnimStyle, formAnim] = useSpring(() => ({
    from: { x: 0 },
    reset: true,
  }));
  const [hasInputErr, setHasInputErr] = useState(false);
  const [server, setServer] = useAsyncInitState<ServerRegion>(
    'Global',
    async (set) => {
      const serverRegion = await storage.getData('serverRegion');
      set(serverRegion);
    }
  );
  const serverSelectMenu = useDialogTrans();

  const onInput = (type: 'email' | 'password', value: string) => {
    setHasInputErr(false);
    setAccount((state) => ({ ...state, [type]: value }));
  };

  const submit = async () => {
    const { email, password } = account;
    if (email.length === 0 || password.length === 0) {
      formAnim.start({ from: { x: 0 }, to: { x: 1 } });
      setHasInputErr(true);
      return setErrorMessage(t('settings_login_failed'));
    }

    try {
      isLoadingRef.current = true;
      loader.on();
      const { userId } = await API.login(account);
      const data = await API.validateUser(userId);
      const plantRecords = await storage.getData('plantRecords');
      await syncPlants(plantRecords, {
        lastSyncedAt: new Date(0).toISOString(),
        onError: async () => {
          showToast(t('settings_status_fail'));
          await API.logout();
          throw new Error('Sync failed');
        },
      });
      storage.set(data);
      close();
    } catch (error) {
      setHasInputErr(true);
      const e = error as AxiosError;

      if (e?.response?.status === 488) {
        API.logout();
        showBlockLoginDialog();
      }

      if (e?.response?.status === 403) {
        return setErrorMessage(t('settings_login_failed'));
      }

      if (e?.response?.status === 481 || e?.response?.status === 404) {
        return setErrorMessage(t('login_error_message_server'));
      }

      return setErrorMessage(
        t('settings_login_failed_error_code') + e?.response?.status || 'Unknown'
      );
    } finally {
      isLoadingRef.current = false;
      loader.off();
    }
  };

  const selectServer = (server: ServerRegion) => {
    setServer(server);
    API.setAPIBaseURLByServerRegion(server);
    storage.set({ serverRegion: server });
    serverSelectMenu.off();
  };

  return trans(
    (style, canShow) =>
      canShow && (
        <S.DimView
          style={{ opacity: style.opacity }}
          onClick={({ target, currentTarget }) => {
            if (isLoadingRef.current) return;
            target === currentTarget && close();
          }}
        >
          <S.Dialog style={{ transform: style.transform }}>
            <S.Title>{t('settings_login_portal')}</S.Title>
            <S.ServerSelect onClick={serverSelectMenu.on}>
              <S.SelectIcon src={require('options/images/global.svg')} />
              {(() => {
                switch (server) {
                  case 'Global':
                    return t('global_server');
                  case 'China':
                    return t('china_server');
                }
              })()}
              <S.SelectArrow src={require('options/images/select-arrow.svg')} />
            </S.ServerSelect>
            {errorMessage && <S.ErrorMessage>{errorMessage}</S.ErrorMessage>}
            <S.Form>
              <S.Inputs
                style={{
                  transform: formAnimStyle.x
                    .to({
                      range: [0, 0.01, 0.2, 0.4, 0.6, 0.8, 1],
                      output: [0, 30, -30, 15, -15, 8, 0],
                    })
                    .to((x) => `translateX(${x}px)`),
                }}
              >
                <S.Input
                  hasError={hasInputErr}
                  type="email"
                  placeholder={t('settings_login_email')}
                  onChange={(event) => onInput('email', event.target.value)}
                  value={account.email}
                />
                <S.Input
                  hasError={hasInputErr}
                  type="password"
                  placeholder={t('settings_login_password')}
                  onChange={(event) => onInput('password', event.target.value)}
                  value={account.password}
                />
              </S.Inputs>
              <S.ConfirmButton
                onClick={(event) => {
                  event.preventDefault();
                  submit();
                }}
                style={{ marginTop: '32px', alignSelf: 'center' }}
              >
                {t('settings_login_btn')}
              </S.ConfirmButton>
            </S.Form>
            {serverSelectMenu.trans(
              (style, canShow) =>
                canShow && (
                  <S.SelectMenuOverlay
                    style={{ opacity: style.opacity }}
                    onClick={({ target, currentTarget }) =>
                      target === currentTarget && serverSelectMenu.off()
                    }
                  >
                    <S.SelectMenu style={{ transform: style.transform }}>
                      <S.SelectMenuTitle>
                        {t('login_choose_server')}
                      </S.SelectMenuTitle>
                      <S.Options>
                        <S.Option
                          selected={server === 'Global'}
                          onClick={() => selectServer('Global')}
                        >
                          {t('global_server')}
                        </S.Option>
                        <S.Option
                          selected={server === 'China'}
                          onClick={() => selectServer('China')}
                        >
                          {t('china_server')}
                        </S.Option>
                      </S.Options>
                    </S.SelectMenu>
                  </S.SelectMenuOverlay>
                )
            )}
            {loader.trans(
              (style, canShow) => canShow && <S.LoaderDimView style={style} />
            )}
          </S.Dialog>
        </S.DimView>
      )
  );
}
