import * as API from 'api';
import * as storage from 'storage';
import { Plant, PlantRecord } from 'types';

type Params = {
  lastSyncedAt: string;
  onStart?: () => void;
  onSucceeded?: (newRecords: PlantRecord[]) => void;
  onError?: (err: unknown) => void;
  onFinished?: () => void;
};

export default async function syncPlants(
  plantRecords: PlantRecord[],
  { lastSyncedAt, onStart, onSucceeded, onError, onFinished }: Params
) {
  try {
    onStart?.();
    const serverPlants = await API.getPlants(lastSyncedAt);
    const serverPlantsById = serverPlants.reduce(
      (map, plant) => ({ ...map, [plant.id!]: plant }),
      {} as Record<number, Plant>
    );
    const tasks = plantRecords.map<Promise<PlantRecord>>(async (record) => {
      if (record.syncStatus === 'Succeeded') {
        return {
          ...record,
          plant: serverPlantsById[record.plant.id!] || record.plant,
        };
      }
      if (record.syncStatus === 'Pending' || record.syncStatus === 'Failed') {
        if (record.plant.id) {
          const updatedPlant = await API.updatePlant(record.plant);
          return { ...record, plant: updatedPlant, syncStatus: 'Succeeded' };
        }
        const createdPlant = await API.createPlant(record.plant);
        return { ...record, plant: createdPlant, syncStatus: 'Succeeded' };
      }
      return record;
    });
    const updatedPlantRecords = await Promise.all(tasks);
    const localPlantIds = updatedPlantRecords.map(({ plant }) => plant.id!);
    const newPlantRecordsFromServer = serverPlants
      .filter(({ id }) => localPlantIds.includes(id!) === false)
      .map<PlantRecord>((plant) => ({ plant, syncStatus: 'Succeeded' }));
    const newPlantRecords = [
      ...updatedPlantRecords,
      ...newPlantRecordsFromServer,
    ].sort(
      (a, b) =>
        new Date(b.plant.startTime).getTime() -
        new Date(a.plant.startTime).getTime()
    );
    await storage.set({
      plantRecords: newPlantRecords,
      lastSyncedAt: new Date().toISOString(),
    });
    onSucceeded?.(newPlantRecords);
  } catch (err) {
    if (onError) {
      onError(err);
    } else {
      throw err;
    }
  } finally {
    onFinished?.();
  }
}
