import { atom } from 'jotai';
import { atomWithDefault } from 'jotai/utils';
import * as storage from 'storage';
import { SiteBlockingMode } from 'types';
import { sendMessage } from 'utils';

const optionsPropsAtom = atom(
  async () => await sendMessage({ type: 'GET_OPTIONS_PROPS', payload: null })
);

export const toastAtom = atom<{ canShow: boolean; text: string }>({
  canShow: false,
  text: '',
});

export const showToastAtom = atom(null, (get, set, text: string) => {
  set(toastAtom, { ...get(toastAtom), canShow: true, text });
});

export const userAtom = atomWithDefault((get) => get(optionsPropsAtom).user);

export const allowListAtom = atomWithDefault(
  (get) => get(optionsPropsAtom).allowList
);

export const blockListAtom = atomWithDefault(
  (get) => get(optionsPropsAtom).blockList
);

export const siteListUpdateAtom = atom(
  null,
  (_get, set, update: [SiteBlockingMode, string[]]) => {
    const [siteBlockingMode, siteList] = update;
    storage.setData(
      siteBlockingMode === 'Allow' ? 'allowList' : 'blockList',
      siteList
    );
    set(siteBlockingMode === 'Allow' ? allowListAtom : blockListAtom, siteList);
  }
);

export const plantingStatusAtom = atomWithDefault(
  (get) => get(optionsPropsAtom).plantingStatus
);

export const isInChinaMigrationAtom = atomWithDefault(
  (get) => get(optionsPropsAtom).isInChinaMigration
);

export const canShowLoginDialogAtom = atom(false);

export const showLoginDialogAtom = atom(null, (_get, set) =>
  set(canShowLoginDialogAtom, true)
);

export const tagsAtom = atomWithDefault(
  async () => await storage.getData('tags')
);

export const plantRecordsAtom = atomWithDefault(
  async () => await storage.getData('plantRecords')
);
