import * as API from 'api';
import { AxiosError } from 'axios';
import { afterChinaMigration, onChinaMigration } from 'china-migration';
import * as dateFns from 'date-fns';
import { useAtomValue, useUpdateAtom } from 'jotai/utils';
import * as atoms from 'options/atom';
import Forest from 'options/components/Forest';
import PlantRecords from 'options/components/PlantRecords';
import SyncIcon from 'options/images/sync.svg';
import syncOnePlant from 'options/syncOnePlant';
import syncPlants from 'options/syncPlants';
import React, { useEffect, useState } from 'react';
import * as storage from 'storage';
import { PlantRecord } from 'types';
import { getDateLocale, t } from 'utils';
import * as S from './styles';

const dateLocale = getDateLocale();

export default function History() {
  const showToast = useUpdateAtom(atoms.showToastAtom);
  const showLoginDialog = useUpdateAtom(atoms.showLoginDialogAtom);
  const user = useAtomValue(atoms.userAtom);
  const isInChinaMigration = useAtomValue(atoms.isInChinaMigrationAtom);
  const plantRecords = useAtomValue(atoms.plantRecordsAtom);
  const [date, setDate] = useState(dateFns.startOfToday());
  const [isSyncing, setIsSyncing] = useState(false);
  const [plantRecordToSync, setPlantRecordToSync] =
    useState<PlantRecord | null>(null);
  const hasLoggedIn = user !== null;

  const syncAll = async () => {
    if (isSyncing) return;
    if (plantRecordToSync) return;
    if (hasLoggedIn === false) return;
    const lastSyncedAt = await storage.getData('lastSyncedAt');
    try {
      await syncPlants(plantRecords, {
        lastSyncedAt,
        onStart: () => setIsSyncing(true),
      });
      const data = await API.validateUser(user!.id);
      await storage.set(data);
      showToast(t('settings_sync_completed'));
    } catch (err) {
      const error = err as AxiosError;
      if (error?.response?.status === 488) return onChinaMigration();
      if (error?.response?.status === 481) return afterChinaMigration();
      showToast(t('notification_title_upload_failed'));
    } finally {
      setIsSyncing(false);
    }
  };

  useEffect(() => {
    if (plantRecordToSync === null) return;
    if (isSyncing) return;
    if (hasLoggedIn === false) return;
    syncOnePlant(plantRecords, plantRecordToSync, {
      onSucceeded: () => showToast(t('settings_sync_completed')),
      onError: () => showToast(t('notification_title_upload_failed')),
      onFinished: () => setPlantRecordToSync(null),
    });
  }, [plantRecordToSync]);

  const toNextDay = () => {
    const nextDay = dateFns.addDays(date, 1);
    if (dateFns.isFuture(nextDay)) return;
    setDate(nextDay);
  };

  const toPrevDay = () => setDate((date) => dateFns.subDays(date, 1));

  if (plantRecords === null) return null;

  const todaysPlantRecords = plantRecords.filter(({ plant }) => {
    const plantDate = new Date(plant.startTime);
    return dateFns.isSameDay(date, plantDate);
  });
  const isNextDayInFuture = dateFns.isFuture(dateFns.addDays(date, 1));

  return (
    <S.Main>
      <S.SyncButton
        onClick={hasLoggedIn ? syncAll : showLoginDialog}
        $isDisabled={
          isInChinaMigration || isSyncing || plantRecordToSync !== null
        }
      >
        {hasLoggedIn && <S.SyncIcon src={SyncIcon} isSyncing={isSyncing} />}
        {hasLoggedIn ? t('settings_sync_app_btn') : t('settings_no_sync_btn')}
      </S.SyncButton>
      <S.PlantTitleGroup>
        <S.Title>
          {dateFns.format(date, 'MMMM do (E)', { locale: dateLocale })}{' '}
          {t('settings_my_forest')}
        </S.Title>
      </S.PlantTitleGroup>
      <S.CanvasRow>
        <S.ArrowLeft onClick={toPrevDay} />
        <Forest plantRecords={todaysPlantRecords} todayTime={date.getTime()} />
        {isNextDayInFuture === false && <S.ArrowRight onClick={toNextDay} />}
      </S.CanvasRow>
      <S.Title style={{ marginTop: 16 }}>{t('settings_my_history')}</S.Title>
      <PlantRecords
        canShowSyncBtn={isInChinaMigration === false && hasLoggedIn}
        plantRecords={todaysPlantRecords}
        isSyncingAll={isSyncing}
        plantRecordToSync={plantRecordToSync}
        setPlantRecordToSync={setPlantRecordToSync}
      />
      <S.Buttons>
        <S.BtnChangeDay onClick={toPrevDay}>
          <S.StaticArrowLeft />
          {dateFns.format(dateFns.subDays(date, 1), 'MMMM do (E)', {
            locale: dateLocale,
          })}
        </S.BtnChangeDay>
        {isNextDayInFuture ? (
          <div />
        ) : (
          <S.BtnChangeDay onClick={toNextDay}>
            {dateFns.format(dateFns.addDays(date, 1), 'MMMM do (E)', {
              locale: dateLocale,
            })}
            <S.StaticArrowRight />
          </S.BtnChangeDay>
        )}
      </S.Buttons>
    </S.Main>
  );
}
