import { useAtomValue, useUpdateAtom } from 'jotai/utils';
import {
  allowListAtom,
  blockListAtom,
  plantingStatusAtom,
  showToastAtom,
  siteListUpdateAtom,
} from 'options/atom';
import React, { useState } from 'react';
import reactStringReplace from 'react-string-replace';
import * as tldts from 'tldts';
import { SiteBlockingMode } from 'types';
import * as utils from 'utils';
import * as S from './styles';

type Props = {
  siteBlockingMode: SiteBlockingMode;
};

export default function SiteList({ siteBlockingMode }: Props) {
  const showToast = useUpdateAtom(showToastAtom);
  const plantingStatus = useAtomValue(plantingStatusAtom);
  const siteList = useAtomValue(
    siteBlockingMode === 'Allow' ? allowListAtom : blockListAtom
  );
  const setSiteList = useUpdateAtom(siteListUpdateAtom);
  const [urlValue, setUrlValue] = useState('');

  const onInput = (value: string) => {
    setUrlValue(value);
  };

  const onKeyDown = (key: string) => {
    if (key !== 'Enter') return;
    const result = tldts.parse(urlValue);
    if (result.domain === null) {
      return showToast(utils.t('settings_toast_error'));
    }
    if (siteList.includes(result.hostname!)) {
      return showToast(utils.t('settings_toast_existed'));
    }
    setSiteList([siteBlockingMode, [...siteList, result.hostname!]]);
    setUrlValue('');
  };

  const removeFromList = (targetIndex: number) => {
    const filteredSiteList = siteList.filter(
      (_, index) => index !== targetIndex
    );
    setSiteList([siteBlockingMode, filteredSiteList]);
  };

  return (
    <>
      {plantingStatus === 'Started' && (
        <S.Warning>
          <S.WarningIcon src={require('options/images/sync-failed.svg')} />
          <S.WarningText>{utils.t('extension_cannot_edit')}</S.WarningText>
        </S.Warning>
      )}
      <S.Description>
        <S.DescriptionTitle>
          {reactStringReplace(
            utils.t('settings_blocklist_allowlist_description'),
            '[%s]',
            (_, index) => (
              <S.HighLight key={index}>
                {siteBlockingMode === 'Allow'
                  ? utils.t('settings_allowlist_can')
                  : utils.t('settings_blocklist_cannot')}
              </S.HighLight>
            )
          )}
        </S.DescriptionTitle>
        <S.DescriptionContent>
          {utils.t('settings_mode_switch')}
        </S.DescriptionContent>
      </S.Description>
      <S.EditArea disabled={plantingStatus === 'Started'}>
        <S.InputField
          placeholder={utils.t('settings_newblackbox_placeholder')}
          value={urlValue}
          onChange={({ target }) => onInput(target.value)}
          onKeyDown={({ key }) => onKeyDown(key)}
        />
        <S.List>
          {siteList.map((url, index) => (
            <S.Item key={url}>
              <S.Icon
                src={`https://www.google.com/s2/favicons?sz=64&domain_url=${url}`}
              />
              <S.Text>{url}</S.Text>
              <S.BtnRemove
                src={require('options/images/remove.svg')}
                onClick={() => removeFromList(index)}
              />
            </S.Item>
          ))}
        </S.List>
      </S.EditArea>
    </>
  );
}
