import * as API from 'api';
import * as storage from 'storage';
import { PlantRecord } from 'types';

type ActionProps = {
  onSucceeded?: () => void;
  onError?: () => void;
  onFinished?: () => void;
};

export default async function syncOnePlant(
  plantRecords: PlantRecord[],
  plantRecordToSync: PlantRecord,
  { onSucceeded, onError, onFinished }: ActionProps
) {
  try {
    const tasks = plantRecords.map<Promise<PlantRecord>>(async (record) => {
      if (record.plant.startTime !== plantRecordToSync.plant.startTime) {
        return record;
      }
      if (plantRecordToSync.syncStatus === 'Succeeded') {
        const serverPlants = await API.getPlantsByStartTime(
          plantRecordToSync.plant.startTime
        );
        const targetPlant =
          serverPlants.find(({ id }) => id === plantRecordToSync.plant.id) ||
          null;
        if (targetPlant === null) return record;
        return { ...record, plant: targetPlant };
      }
      if (plantRecordToSync.plant.id) {
        const updatedPlant = await API.updatePlant(plantRecordToSync.plant);
        return {
          ...record,
          plant: updatedPlant,
          syncStatus: 'Succeeded',
        };
      }
      const createdPlant = await API.createPlant(plantRecordToSync.plant);
      return {
        ...record,
        plant: createdPlant,
        syncStatus: 'Succeeded',
      };
    });
    const syncedPlantRecords = await Promise.all(tasks);
    await storage.setData('plantRecords', syncedPlantRecords);
    onSucceeded?.();
  } catch (err) {
    const updatedPlantRecords = plantRecords.map<PlantRecord>((record) => {
      if (record.plant.startTime === plantRecordToSync.plant.startTime) {
        return { ...record, syncStatus: 'Failed' };
      }
      return record;
    });
    storage.set({ plantRecords: updatedPlantRecords });
    onError?.();
  } finally {
    onFinished?.();
  }
}
