import axios from 'axios';
import camelcaseKeys from 'camelcase-keys';
import manifest from 'manifest';
import snakecaseKeys from 'snakecase-keys';
import type {
  Boost,
  Plant,
  ServerRegion,
  Tag,
  User,
  UserLoginData,
} from 'types';
import { t } from 'utils';
import browser from 'webextension-polyfill';

const isFirefox = browser.runtime.getURL('.').includes('moz');

axios.defaults.params = {
  seekrua: `extension_${isFirefox ? 'firefox' : 'chrome'}-${manifest.version}`,
};

// Make sure to call this function on every runtime that uses Forest server API
export const setAPIBaseURLByServerRegion = (serverRegion: ServerRegion) => {
  if (process.env.NODE_ENV === 'development') {
    switch (serverRegion) {
      case 'Global':
        axios.defaults.baseURL = 'https://forest.staging.seekrtech.com/api/v1';
        return;
      case 'China':
        axios.defaults.baseURL =
          'https://forest.staging-china.upwardsware.com/api/v1';
        return;
      default:
        throw new Error('Invalid server region');
    }
  }
  switch (serverRegion) {
    case 'Global':
      axios.defaults.baseURL = 'https://c88fef96.forestapp.cc/api/v1';
      return;
    case 'China':
      axios.defaults.baseURL = 'https://forest-china.upwardsware.com/api/v1';
      return;
    default:
      throw new Error('Invalid server region');
  }
};

export const getTags = async () => {
  const { data } = await axios.get<{
    update_since: string;
    tags: Tag[];
  }>('/tags');
  return camelcaseKeys(data.tags, { deep: true });
};

export const getUnlockedPlantIds = async () => {
  const { data } = await axios.get<{ gid: number }[]>('/tree_types/unlocked');
  return data.map(({ gid }) => gid);
};

export const getUserBoost = async (userId: number) => {
  const { data } = await axios.get(`/users/${userId}/boost`);
  return camelcaseKeys(data, { deep: true }) as Boost;
};

export const getPlantsByStartTime = async (startTime: string) => {
  const toDate = new Date(new Date(startTime).getTime() + 1000);
  const { data } = await axios.get('/plants', {
    params: {
      from_date: startTime,
      to_date: toDate,
    },
  });
  return camelcaseKeys(data, { deep: true }) as Plant[];
};

export const login = async (loginBody: UserLoginData) => {
  const { data } = await axios.post<{ user_id: number }>('/sessions', {
    session: loginBody,
  });
  return camelcaseKeys(data);
};

export const logout = () => axios.delete('/sessions/signout', { data: 'null' });

export const getUser = async (userId: number) => {
  const { data } = await axios.get(`/users/${userId}`);
  return camelcaseKeys(data) as User;
};

export const createPlant = async (plant: Plant) => {
  const { data } = await axios.post('/plants', {
    plant: snakecaseKeys(plant, { deep: true }),
  });
  return camelcaseKeys(data, { deep: true }) as Plant;
};

export const updatePlant = async (plant: Plant) => {
  const { data } = await axios.put(`/plants/${plant.id}`, {
    plant: snakecaseKeys(plant, { deep: true }),
  });
  return camelcaseKeys(data, { deep: true }) as Plant;
};

export const getPlants = async (lastSyncedAt?: string) => {
  const { data } = await axios.get('/plants', {
    ...(lastSyncedAt && { params: { from_date: lastSyncedAt } }),
  });
  return camelcaseKeys(data, { deep: true }) as Plant[];
};

export const validateUser = async (userId: number) => {
  const [user, boost, tags, unlockedPlantIds] = await Promise.all([
    getUser(userId),
    getUserBoost(userId),
    getTags(),
    getUnlockedPlantIds(),
  ]);
  const availableTags = (
    tags.findIndex(({ tagId }) => tagId === 0) < 0
      ? [{ title: t('plant_tag_0'), tagId: 0, deleted: false }, ...tags]
      : tags
  ).filter(({ deleted }) => deleted === false);
  return {
    user: { ...user, ...boost },
    tags: availableTags,
    unlockedPlantIds,
  };
};

export const getCurrentUserId = async (rememberToken: string) => {
  const { data } = await axios.get<{ user_id: number }>('/users/current', {
    data: { remember_token: rememberToken },
  });
  return data.user_id;
};
