import { animated } from '@react-spring/web';
import * as dateFns from 'date-fns';
import useItemDialogTrans from 'hooks/useItemDialogTrans';
import { useAtomValue } from 'jotai/utils';
import { tagsAtom } from 'options/atom';
import PlantEditDialog from 'options/components/PlantEditDialog';
import React, { useEffect } from 'react';
import * as storage from 'storage';
import { Plant, PlantRecord } from 'types';
import { t } from 'utils';
import { getPlantImage } from './helpers';
import * as S from './styles';

type Props = {
  canShowSyncBtn: boolean;
  plantRecords: PlantRecord[];
  isSyncingAll: boolean;
  plantRecordToSync: PlantRecord | null;
  setPlantRecordToSync: React.Dispatch<
    React.SetStateAction<PlantRecord | null>
  >;
};

export default function PlantRecords({
  canShowSyncBtn,
  plantRecords,
  isSyncingAll,
  plantRecordToSync,
  setPlantRecordToSync,
}: Props) {
  const tags = useAtomValue(tagsAtom);
  const plantEditDialog = useItemDialogTrans<Plant>();
  useEffect(() => {
    plantEditDialog.off();
  }, [plantRecords]);

  type SaveModifiedPlantProps = {
    startTime: string;
    note: string;
    tag: number;
  };
  const saveModifiedPlant = ({
    startTime,
    note,
    tag,
  }: SaveModifiedPlantProps) => {
    const modifiedPlantRecords = plantRecords.map(
      (plantRecord): PlantRecord => {
        if (plantRecord.plant.startTime === startTime) {
          return {
            ...plantRecord,
            syncStatus: 'Pending',
            plant: {
              ...plantRecord.plant,
              tag,
              note,
            },
          };
        }
        return plantRecord;
      }
    );
    storage.setData('plantRecords', modifiedPlantRecords);
  };

  const getPlantingDuration = (start: string, end: string) =>
    Math.round((new Date(end).getTime() - new Date(start).getTime()) / 60000);

  const getTagTitleById = (id: number) => {
    const targetTag = tags.find(({ tagId }) => tagId === id);
    return targetTag?.title || t('plant_tag_0');
  };

  return (
    <>
      {plantRecords.map((plantRecord) => {
        const { syncStatus, plant } = plantRecord;
        const isSyncing =
          isSyncingAll ||
          plant.startTime === plantRecordToSync?.plant?.startTime;
        return (
          <S.Record
            key={plant.startTime}
            onClick={() => plantEditDialog.on(plant)}
          >
            <S.PlantBallCover>
              <S.Plant
                src={getPlantImage({
                  treeTypeGid: plant.treeTypeGid,
                  isSuccess: plant.isSuccess,
                })}
              />
            </S.PlantBallCover>
            <S.PlantInfo>
              <S.Row>
                <S.Time>
                  {dateFns.format(new Date(plant.startTime), 'HH:mm')} ~{' '}
                  {dateFns.format(new Date(plant.endTime), 'HH:mm')}
                </S.Time>
                <S.Trees>
                  {plant.trees.map(({ isDead }, index) => (
                    <S.Tree
                      key={index}
                      src={
                        isDead
                          ? require('options/images/dead-tree.svg')
                          : require('options/images/tree.svg')
                      }
                    />
                  ))}
                </S.Trees>
              </S.Row>
              <S.Row style={{ marginTop: 7 }}>
                <S.TimeIcon src={require('options/images/time.svg')} />
                <S.TimeText>
                  {getPlantingDuration(plant.startTime, plant.endTime)}
                </S.TimeText>
                <S.Tag>{getTagTitleById(plant.tag)}</S.Tag>
                <S.Note>{plant.note}</S.Note>
              </S.Row>
            </S.PlantInfo>
            {canShowSyncBtn && (
              <S.SyncStatusIconBox>
                <S.SyncStatusIcon
                  $isSyncing={isSyncing}
                  as={animated.img}
                  src={
                    syncStatus === 'Pending' || isSyncing
                      ? require('options/images/sync-await.svg')
                      : syncStatus === 'Succeeded'
                      ? require('options/images/sync-success.svg')
                      : require('options/images/sync-failed.svg')
                  }
                  onClick={(event: React.MouseEvent<HTMLImageElement>) => {
                    if (isSyncing) return;
                    event.stopPropagation();
                    setPlantRecordToSync(plantRecord);
                  }}
                />
                <S.ToolTip>
                  {syncStatus === 'Pending' || isSyncing
                    ? t('settings_status_waitforsync')
                    : syncStatus === 'Succeeded'
                    ? t('settings_status_200')
                    : t('settings_status_fail')}
                </S.ToolTip>
              </S.SyncStatusIconBox>
            )}
          </S.Record>
        );
      })}
      {plantEditDialog.trans(
        (spring, plant) =>
          plant && (
            <PlantEditDialog
              spring={spring}
              tags={tags}
              plant={plant}
              close={plantEditDialog.off}
              save={saveModifiedPlant}
            />
          )
      )}
    </>
  );
}
