import { defaultTags, defaultUnlockedPlantIds } from 'const';
import * as storage from 'storage';
import { PlantRecord } from 'types';

export const onChinaMigration = () => {
  storage.set({ isInChinaMigration: true });
};

export const afterChinaMigration = async (
  cleanState?: (plantRecords: PlantRecord[]) => void
) => {
  const plantRecords = await storage.getData('plantRecords');
  const localPlantRecords = plantRecords.filter(
    ({ plant }) => plant.id === undefined
  );
  storage.set({
    isInChinaMigration: false,
    user: null,
    tags: defaultTags,
    unlockedPlantIds: defaultUnlockedPlantIds,
    plantRecords: localPlantRecords,
  });
  cleanState?.(localPlantRecords);
};
