import Carousel from 'options/components/Carousel';
import appStoreBadge from 'options/images/app-store-badge.svg';
import googlePlayBadge from 'options/images/google-play-badge.png';
import React from 'react';
import { t } from 'utils';
import * as S from './styles';

const toStore = (store: 'AppStore' | 'GooglePlay') => {
  switch (store) {
    case 'AppStore':
      window.open(
        'https://apps.apple.com/app/apple-store/id866450515?pt=690486&ct=new-chrome-extension&mt=8'
      );
      break;

    case 'GooglePlay':
      window.open(
        'https://play.google.com/store/apps/details?id=cc.forestapp&referrer=utm_source%3Dchromeextension%26utm_campaign%3Dnewchromeextension'
      );
      break;
  }
};

export default function About() {
  return (
    <S.Main>
      <S.Title>{t('extension_about_forest_title')}</S.Title>
      <S.Description>{t('extension_about_forest_context')}</S.Description>
      <Carousel />
      <S.StoreBadges>
        <S.StoreBadge
          src={googlePlayBadge}
          onClick={() => toStore('GooglePlay')}
        />
        <S.StoreBadge src={appStoreBadge} onClick={() => toStore('AppStore')} />
      </S.StoreBadges>
    </S.Main>
  );
}
