import { useEffect, useState } from 'react';

export default function useAsyncInitState<T>(
  initialState: T,
  get: (set: React.Dispatch<React.SetStateAction<T>>) => void
) {
  const [state, setState] = useState<T>(initialState);
  useEffect(() => {
    get(setState);
  }, []);
  return [state, setState] as const;
}
