import useAsyncInitState from 'hooks/useAsyncInitState';
import leftArrowImage from 'options/images/arrow-left.svg';
import rightArrowImage from 'options/images/arrow-right.svg';
import React, { useState } from 'react';
import { importAboutImages } from './helpers';
import * as S from './styles';

const CAROUSEL_MIN_INDEX = 0;
const CAROUSEL_MAX_INDEX = 5;

export default function Carousel() {
  const [carouselIndex, setCarouselIndex] = useState(0);
  const [images] = useAsyncInitState<string[]>([], async (set) => {
    const images = await importAboutImages();
    set(images);
  });

  const updateCarousel = (action: 'Next' | 'Prev') => {
    switch (action) {
      case 'Next':
        setCarouselIndex((previousIndex) =>
          previousIndex === CAROUSEL_MAX_INDEX
            ? previousIndex
            : previousIndex + 1
        );
        break;

      case 'Prev':
        setCarouselIndex((previousIndex) =>
          carouselIndex === CAROUSEL_MIN_INDEX
            ? previousIndex
            : previousIndex - 1
        );
        break;

      default:
        break;
    }
  };

  return (
    <S.Carousel>
      {carouselIndex !== CAROUSEL_MIN_INDEX && (
        <S.Arrow
          src={leftArrowImage}
          direction="Left"
          onClick={() => updateCarousel('Prev')}
        />
      )}
      <S.CarouselImages>
        {images.map((src) => (
          <S.CarouselImage key={src} src={src} carouselIndex={carouselIndex} />
        ))}
      </S.CarouselImages>
      {carouselIndex !== CAROUSEL_MAX_INDEX && (
        <S.Arrow
          src={rightArrowImage}
          direction="Right"
          onClick={() => updateCarousel('Next')}
        />
      )}
    </S.Carousel>
  );
}
