import * as API from 'api';
import { AxiosError } from 'axios';
import { afterChinaMigration, onChinaMigration } from 'china-migration';
import { defaultTags, defaultUnlockedPlantIds } from 'const';
import useFadeInOut from 'hooks/useFadeInOut';
import { useAtomValue, useUpdateAtom } from 'jotai/utils';
import {
  isInChinaMigrationAtom,
  showLoginDialogAtom,
  showToastAtom,
  userAtom,
} from 'options/atom';
import syncPlants from 'options/syncPlants';
import React, { useEffect } from 'react';
import { useInView } from 'react-intersection-observer';
import * as storage from 'storage';
import { t } from 'utils';
import * as S from './styles';

type Props = {
  globalLoader: ReturnType<typeof useFadeInOut>;
};

export default function UserInfo({ globalLoader }: Props) {
  const showToast = useUpdateAtom(showToastAtom);
  const user = useAtomValue(userAtom);
  const showLoginDialog = useUpdateAtom(showLoginDialogAtom);
  const isInChinaMigration = useAtomValue(isInChinaMigrationAtom);
  const [inViewRef, canHideSmallInfo] = useInView({
    rootMargin: '0px 0px 0px 0px',
  });

  const clearUser = async () => {
    const plantRecords = await storage.getData('plantRecords');
    const filteredPlantRecords = plantRecords.filter(
      ({ plant }) => plant.id === undefined
    );
    storage.set({
      user: null,
      tags: defaultTags,
      unlockedPlantIds: defaultUnlockedPlantIds,
      plantRecords: filteredPlantRecords,
    });
  };

  const logout = async () => {
    try {
      globalLoader.on();
      const { plantRecords, lastSyncedAt } = await storage.get([
        'plantRecords',
        'lastSyncedAt',
      ]);
      await syncPlants(plantRecords, {
        lastSyncedAt,
        onSucceeded: () => storage.set({ plantRecords: [] }),
      });
      await API.logout();
      clearUser();
    } catch (err) {
      showToast(t('settings_status_fail'));
      if ((err as AxiosError)?.response?.status === 488) {
        onChinaMigration();
      }
    } finally {
      globalLoader.off();
    }
  };

  useEffect(() => {
    (async function revalidateUser() {
      if (user) {
        try {
          const data = await API.validateUser(user.id);
          storage.set(data);
        } catch (err) {
          const e = err as AxiosError;
          if (e?.response?.status === 488) return onChinaMigration();
          if (e?.response?.status === 481) return afterChinaMigration();
          e?.response?.status === 403 && clearUser();
        }
      }
    })();
  }, []);

  const { healthTreeCount = 0, deadTreeCount = 0 } = user || {};
  const successRate = (
    (healthTreeCount / (healthTreeCount + deadTreeCount)) *
    100
  ).toFixed(0);

  return (
    <S.UserInfo>
      <S.Avatar
        src={user?.avatarUrl || require('options/images/avatar.png')}
        style={{ opacity: canHideSmallInfo ? 1 : 0 }}
      />
      {user ? (
        <S.AuthedInfoBox style={{ opacity: canHideSmallInfo ? 1 : 0 }}>
          <S.UserNameRow>
            <S.UserName>{user.name}</S.UserName>
            <S.LogoutButton onClick={logout} isDisabled={isInChinaMigration}>
              {t('settings_tab_logout')}
            </S.LogoutButton>
          </S.UserNameRow>
          <S.StatusRow>
            <S.StatusCol>
              <S.StatusIcon src={require('options/images/tree.svg')} />
              <S.StatusText>{healthTreeCount}</S.StatusText>
              <S.ToolTip>{t('settings_hover_healthy_tree')}</S.ToolTip>
            </S.StatusCol>
            <S.StatusCol>
              <S.StatusIcon src={require('options/images/success-rate.svg')} />
              <S.StatusText>{successRate}%</S.StatusText>
              <S.ToolTip style={{ left: '16px' }}>
                {t('settings_hover_success_rate')}
              </S.ToolTip>
            </S.StatusCol>
            <S.StatusCol>
              <S.StatusIcon src={require('options/images/coin.svg')} />
              <S.StatusText>{user.coin}</S.StatusText>
              <S.ToolTip style={{ left: '16px' }}>
                {t('settings_hover_coin')}
              </S.ToolTip>
            </S.StatusCol>
          </S.StatusRow>
        </S.AuthedInfoBox>
      ) : (
        <S.UnauthInfoBox>
          <S.LoginButton onClick={showLoginDialog}>
            {t('settings_login_btn')}
          </S.LoginButton>
          <S.RegisterTip>
            {t('settings_no_account1')}
            <S.StoreLink
              target="_blank"
              href="https://apps.apple.com/tw/app/forest-stay-focused-stop-phubbing/id866450515"
            >
              {t('settings_no_account_iosapp')}
            </S.StoreLink>
            <span>{t('settings_no_account_or')}</span>
            <S.StoreLink
              target="_blank"
              href="https://play.google.com/store/apps/details?id=cc.forestapp"
            >
              {t('settings_no_account_androidapp')}
            </S.StoreLink>
            {t('settings_no_account2')}
          </S.RegisterTip>
        </S.UnauthInfoBox>
      )}
      <S.InViewObservable ref={inViewRef} />
      <S.SmallInfoRow style={{ opacity: canHideSmallInfo ? 0 : user ? 1 : 0 }}>
        <S.SmallAvatar
          src={user?.avatarUrl || require('options/images/avatar.png')}
        />
        <S.SmallName>{user?.name}</S.SmallName>
        <S.LogoutButton onClick={logout} isDisabled={isInChinaMigration}>
          {t('settings_tab_logout')}
        </S.LogoutButton>
      </S.SmallInfoRow>
    </S.UserInfo>
  );
}
