import plants from '@forest/plants';
import React from 'react';
import { Image, Layer, Stage } from 'react-konva';
import { PlantRecord } from 'types';
import useImage from 'use-image';

const randomBySeed = (seed: number) => {
  const num = Math.sin(seed === 0 ? 1 : seed) * 10000;
  return num - Math.floor(num);
};

const shuffle = (array: Pos[], seed: number) => {
  const copiedArray = [...array];
  let i = array.length - 1;

  while (i) {
    const j = Math.floor(randomBySeed(seed) * i);
    i--;
    const temp = copiedArray[i];
    copiedArray[i] = copiedArray[j];
    copiedArray[j] = temp;
    ++seed;
  }

  return copiedArray;
};

type PieceProps = {
  x: number;
  y: number;
  w: number;
  h: number;
};

const GroundPieceImg = ({ x, y, w, h }: PieceProps) => {
  const [image] = useImage(require('options/images/ground-piece.png'));
  return <Image image={image} width={w} height={h} x={x} y={y} />;
};

const GroundL = ({ x, y, h, w }: PieceProps) => {
  const [image] = useImage(require('options/images/ground-left.png'));
  return <Image image={image} width={w} height={h} x={x} y={y} />;
};

const GroundR = ({ x, y, h, w }: PieceProps) => {
  const [image] = useImage(require('options/images/ground-right.png'));
  return <Image image={image} width={w} height={h} x={x} y={y} />;
};

type TreeProps = {
  plantId: number;
  phase: number;
  isDead: boolean;
  w: number;
  x: number;
  y: number;
};

const Tree = ({ plantId, phase, isDead, w, x, y }: TreeProps) => {
  const targetPlant = plants.find((plant) => plant.id === plantId);
  const imageSrc = isDead
    ? targetPlant?.images['dead']
    : targetPlant?.images[phase as keyof typeof targetPlant.images];
  const [image] = useImage(imageSrc || '');
  return <Image image={image} width={w} height={w} x={x} y={y} />;
};

type Pos = { x: number; y: number };

const canvasHeight = 160;
const emptyYSpaceRatio = 0.196375;

type Props = {
  plantRecords: PlantRecord[];
  todayTime: number;
};

export default function Forest({ plantRecords, todayTime }: Props) {
  const trees = plantRecords.flatMap(({ plant }) => plant.trees).reverse();
  const treesCountLimit = Math.min(5000, trees.length);
  const edgeLen = Math.max(
    5,
    Math.ceil(Math.sqrt((treesCountLimit * 100) / 80))
  );
  const piecePosList: Pos[] = [];
  const leftGroundPosList: Pos[] = [];
  const rightGroundPosList: Pos[] = [];
  const earthHeight = canvasHeight * (1 - emptyYSpaceRatio);
  const pieceHeight = earthHeight / (edgeLen + 1);
  const pieceWidth = pieceHeight * 2;
  const treeWidth = pieceWidth * 0.8;
  const treeHeight = treeWidth;
  const treeXOffset = pieceWidth * 0.1;
  const treeYOffset = 0 + pieceHeight * 0.75 - treeHeight;

  const initialPos = {
    x: 220 / 2 - pieceWidth / 2,
    y: canvasHeight - earthHeight,
  };

  for (let i = 0; i < edgeLen; i++) {
    const iPosX = initialPos.x + (pieceWidth / 2) * i;
    const iPosY = initialPos.y + (pieceHeight / 2) * i;

    for (let j = 0; j < edgeLen; j++) {
      const posX = iPosX - j * (pieceWidth / 2);
      const posY = iPosY + j * (pieceHeight / 2);
      piecePosList.push({ x: posX, y: posY });
    }
  }

  const shuffledPiecePosList = shuffle(piecePosList, todayTime);
  const initialGroundLPosX = initialPos.x - (edgeLen - 1) * (pieceWidth / 2);
  const initialGroundLPoxY = initialPos.y + edgeLen * (pieceHeight / 2);

  for (let i = 0; i < edgeLen; i++) {
    leftGroundPosList.push({
      x: initialGroundLPosX + i * (pieceWidth / 2),
      y: initialGroundLPoxY + i * (pieceHeight / 2),
    });
  }

  const initialGroundRPosX = initialPos.x + edgeLen * (pieceWidth / 2);
  const initialGroundRPoxY = initialPos.y + edgeLen * (pieceHeight / 2);

  for (let i = 0; i < edgeLen; i++) {
    rightGroundPosList.push({
      x: initialGroundRPosX - i * (pieceWidth / 2),
      y: initialGroundRPoxY + i * (pieceHeight / 2),
    });
  }

  return (
    <Stage width={220} height={160}>
      <Layer>
        {piecePosList.map(({ x, y }, index) => (
          <GroundPieceImg
            key={index}
            x={x}
            y={y}
            w={pieceWidth}
            h={pieceHeight}
          ></GroundPieceImg>
        ))}
        {leftGroundPosList.map(({ x, y }, index) => (
          <GroundL
            key={index}
            x={x}
            y={y}
            w={pieceWidth / 2}
            h={pieceHeight * 1.5}
          />
        ))}
        {rightGroundPosList.map(({ x, y }, index) => (
          <GroundR
            key={index}
            x={x}
            y={y}
            w={pieceWidth / 2}
            h={pieceHeight * 1.5}
          />
        ))}
        {trees.map((tree, index) => (
          <Tree
            key={index}
            isDead={tree.isDead}
            phase={tree.phase}
            plantId={tree.treeType}
            w={treeWidth}
            x={shuffledPiecePosList[index].x + treeXOffset}
            y={shuffledPiecePosList[index].y + treeYOffset}
          />
        ))}
      </Layer>
    </Stage>
  );
}
