import forestPlants from '@forest/plants';

type GetPlantImageProp = {
  treeTypeGid: number;
  isSuccess: boolean;
};

export const getPlantImage = ({
  treeTypeGid,
  isSuccess,
}: GetPlantImageProp) => {
  const targetPlant = forestPlants.find(({ id }) => id === treeTypeGid) || null;

  if (targetPlant === null) {
    return '';
  }

  if (isSuccess === false) {
    return targetPlant.images.dead;
  }

  return targetPlant.phaseType === 'Four'
    ? targetPlant.images[4]
    : targetPlant.images[7];
};
