import { animated, useTransition } from '@react-spring/web';
import { useAtom } from 'jotai';
import { toastAtom } from 'options/atom';
import React, { useEffect } from 'react';
import styled from 'styled-components';

const S = {
  Toast: styled(animated.div)`
    padding: 10px 20px;
    font-size: 12px;
    line-height: 16px;
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(0, 0, 0, 0.8);
    border-radius: 6px;
    color: #fff;
    display: flex;
    align-items: center;
  `,
  Icon: styled.img`
    width: 20px;
    height: 20px;
    margin-right: 4px;
  `,
};

export default function Toast() {
  const [{ canShow, text }, setToast] = useAtom(toastAtom);
  const trans = useTransition(canShow, {
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0 },
  });
  useEffect(() => {
    if (canShow) {
      setTimeout(() => {
        setToast((toast) => ({ ...toast, canShow: false }));
      }, 4500);
    }
  }, [canShow]);
  return trans(
    (style, canShow) =>
      canShow && (
        <S.Toast style={style}>
          <S.Icon src={require('options/images/toast-info.svg')} />
          {text}
        </S.Toast>
      )
  );
}
