import { SpringValue } from '@react-spring/web';
import React, { useEffect, useState } from 'react';
import { Plant, Tag } from 'types';
import { t } from 'utils';
import * as S from './styles';

type Props = {
  tags: Tag[];
  plant: Plant;
  close: () => void;
  save: (props: { startTime: string; note: string; tag: number }) => void;
  spring: {
    opacity: SpringValue<number>;
    transform: SpringValue<string>;
  };
};

export default function PlantEditDialog({
  plant,
  close,
  save,
  tags,
  spring,
}: Props) {
  const [note, setNote] = useState(plant?.note || '');
  const [tag, setTag] = useState(plant?.tag || 0);
  useEffect(() => {
    if (plant) {
      setTag(plant.tag);
      setNote(plant.note);
    }
  }, [plant]);
  return (
    <S.Overlay style={{ opacity: spring.opacity }}>
      <S.Dialog style={{ transform: spring.transform }}>
        <S.TagSelect
          onChange={({ target }) => setTag(parseInt(target.value))}
          value={tag}
        >
          {tags.map(({ tagId, title }) => (
            <option key={tagId} value={tagId}>
              {title}
            </option>
          ))}
        </S.TagSelect>
        <S.NoteTextarea
          rows={6}
          cols={24}
          value={note}
          onChange={({ target }) => setNote(target.value)}
        ></S.NoteTextarea>
        <S.Buttons>
          <S.BtnCancel onClick={close}>
            {t('main_edit_popup_cancel')}
          </S.BtnCancel>
          <S.BtnConfirm
            onClick={() =>
              save({
                startTime: plant.startTime,
                note,
                tag,
              })
            }
          >
            {t('main_edit_popup_btn')}
          </S.BtnConfirm>
        </S.Buttons>
      </S.Dialog>
    </S.Overlay>
  );
}
