import { getLang } from 'utils';

export const importAboutImages = async () => {
  const lang = getLang();
  const importersByLang = {
    en: [
      () => import('options/images/about-images/en/1.png'),
      () => import('options/images/about-images/en/2.png'),
      () => import('options/images/about-images/en/3.png'),
      () => import('options/images/about-images/en/4.png'),
      () => import('options/images/about-images/en/5.png'),
      () => import('options/images/about-images/en/6.png'),
      () => import('options/images/about-images/en/7.png'),
      () => import('options/images/about-images/en/8.png'),
    ],
    zh_TW: [
      () => import('options/images/about-images/zh_TW/1.png'),
      () => import('options/images/about-images/zh_TW/2.png'),
      () => import('options/images/about-images/zh_TW/3.png'),
      () => import('options/images/about-images/zh_TW/4.png'),
      () => import('options/images/about-images/zh_TW/5.png'),
      () => import('options/images/about-images/zh_TW/6.png'),
      () => import('options/images/about-images/zh_TW/7.png'),
      () => import('options/images/about-images/zh_TW/8.png'),
    ],
    zh_CN: [
      () => import('options/images/about-images/zh_CN/1.png'),
      () => import('options/images/about-images/zh_CN/2.png'),
      () => import('options/images/about-images/zh_CN/3.png'),
      () => import('options/images/about-images/zh_CN/4.png'),
      () => import('options/images/about-images/zh_CN/5.png'),
      () => import('options/images/about-images/zh_CN/6.png'),
      () => import('options/images/about-images/zh_CN/7.png'),
      () => import('options/images/about-images/zh_CN/8.png'),
    ],
  };
  const imageSrcs = await Promise.all(
    importersByLang[lang].map(
      async (importer) => (await importer()).default as string
    )
  );
  return imageSrcs;
};
