import { useTransition } from '@react-spring/web';
import { useCallback, useState } from 'react';

export default function useItemDialogTrans<T>(initailItem?: T) {
  const [item, setItem] = useState<T | null>(initailItem || null);

  const trans = useTransition(item, {
    from: { opacity: 0, transform: 'scale(0.7)' },
    enter: { opacity: 1, transform: 'scale(1)' },
    leave: { opacity: 0, transform: 'scale(0.7)' },
  });

  const off = useCallback(() => {
    setItem(null);
  }, []);

  const on = useCallback((item: T) => {
    setItem(item);
  }, []);

  return {
    trans,
    off,
    on,
  } as const;
}
